import { View, Text, Image, Pressable, Linking } from "react-native";
import styles from "../styles/student_info";
import { FontAwesomeIcon } from "@fortawesome/react-native-fontawesome";
import { faPhone, faHome } from "@fortawesome/free-solid-svg-icons";
import colors from "../../config/colors";
import { useState, useEffect } from "react";

// Import the mock data
const mockStudent = {
  id: "student123",
  name: "Timothy Olimba",
  image: "assets/model.jpg", // Use the link directly for the image
  section: "Year",
  class: "3rd ", // Grade
  phone_no: "7833456", // Phone number without country code
  address: "Kajjansi, Entebbe, Uganda"
};

const mockAttendanceRecords = [
  {
    id: "attendance1",
    presentStudents: ["student123", "student456"],
    absentStudents: ["student789"],
    date: "2025-02-01"
  },
  {
    id: "attendance2",
    presentStudents: ["student123"],
    absentStudents: ["student456", "student789"],
    date: "2025-02-02"
  },
  {
    id: "attendance3",
    presentStudents: ["student456", "student123"],
    absentStudents: [],
    date: "2025-02-03"
  }
];

// Example mock leave appeals data
const mockLeaveAppeals = [
  {
    id: "leave1",
    student: "student123",
    status: "approved",
    reason: "Medical Leave",
    date: "2025-02-05"
  },
  {
    id: "leave2",
    student: "student123",
    status: "approved",
    reason: "Family Emergency",
    date: "2025-02-10"
  }
];

const Attendanceinfo = ({ route }) => {
  // Use mock data instead of route params for testing
  const student = mockStudent; // Replace route.params.student with mockStudent
  const { id, name, image, section, class: grade, phone_no, address } = student;

  const [presentDays, setPresentDays] = useState(0);
  const [absentDays, setAbsentDays] = useState(0);
  const [leaveDays, setLeaveDays] = useState(0);

  const getAttendance = async () => {
    try {
      // Simulate fetching attendance data (you can adjust this based on your mock data)
      let present = 0;
      let absent = 0;

      mockAttendanceRecords.forEach((item) => {
        if (item.presentStudents.includes(id)) {
          present++;
        } else if (item.absentStudents.includes(id)) {
          absent++;
        }
      });

      setPresentDays(present); // Set present days
      setAbsentDays(absent); // Set absent days
    } catch (error) {
      alert(error);
    }
  };

  const getLeaveDays = async () => {
    try {
      // Simulate fetching leave days (you can adjust this based on your mock data)
      const approvedLeaves = mockLeaveAppeals.filter((leave) => leave.student === id && leave.status === "approved");
      setLeaveDays(approvedLeaves.length); // Set leave days
    } catch (error) {
      alert(error);
    }
  };

  useEffect(() => {
    getAttendance();
    getLeaveDays();
  }, []);

  return (
    <View style={styles.container}>
      <View style={styles.head}>
        <View style={styles.box}>
          <Image
            style={styles.img}
            source={{
              uri: image, // Use the image link directly
            }}
          />
          <Text style={styles.name}>{name}</Text>
          <Text style={styles.class}>Class {`${grade}${section}`}</Text>
        </View>
      </View>
      <View style={styles.contactbox}>
        <View style={styles.contact}>
          <Pressable
            onPress={() => Linking.openURL(`tel:+256${phone_no}`)} // Adjusted to match the phone number format
            style={styles.modalContact}
          >
            <View style={styles.contactItem}>
              <FontAwesomeIcon
                icon={faPhone}
                size={20}
                color={colors.lightBlack}
              />
              <Text style={styles.txt}>{`+256 ${phone_no}`}</Text>
            </View>
          </Pressable>

          <View style={styles.contactItem}>
            <FontAwesomeIcon
              icon={faHome}
              size={20}
              color={colors.lightBlack}
            />
            <Text style={styles.txt}>{address}</Text>
          </View>
        </View>
        <View style={styles.attendancebox}>
          <Text style={styles.headertxt}>Attendance</Text>
          <View style={styles.infobox}>
            <View style={styles.databox}>
              <Text style={styles.infotxt}>Present</Text>
              <Text style={styles.datatxt}>{presentDays}</Text>
            </View>
            <View style={styles.databox}>
              <Text style={styles.infotxt}>Absent</Text>
              <Text style={styles.datatxt}>{absentDays}</Text>
            </View>
            <View style={styles.databox}>
              <Text style={styles.infotxt}>Leave</Text>
              <Text style={styles.datatxt}>{leaveDays}</Text>
            </View>
          </View>
        </View>
      </View>
    </View>
  );
};

export default Attendanceinfo;
